@font-face {
  font-family: 'dollar';
  src: url(DollarBill.ttf);
}
@font-face {
  font-family: 'unitedstates';
  src: url(UnitedStates.ttf);
}
@font-face {
  font-family: 'silicon';
  src: url(Silicone.ttf);
}
@font-face {
  font-family: 'siliconhollow';
  src: url(SiliconeHollow.ttf);
}
@font-face {
  font-family: 'siliconfilled';
  src: url(SiliconeFilled.ttf);
}
@font-face {
  font-family: 'showboat';
  src: url(Showboat.ttf);
}
@font-face {
  font-family: 'dalek';
  src: url(Dalek.ttf);
}
.App {
  text-align: center;
}
#atm-title{
  position: relative;
  top: 10.5%;
  left:33%;
  text-align: center;
  line-height: 125%;
  font-size: 50px;
  font-family: 'dalek';
  height: 6%;
  width: 340px;
  font-size: 40px;
  background-color: white;
  text-shadow: -1px -1px white, -2px -2px black;
}
#brick-wall {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
#temple {
position:absolute;
width: 1000px;
height: 700px;
}
#atm-screen {
  position: relative;
  top: 25.5%;
  left: 15.2%;
  height: 405px;
  width: 670px;
  border: 2px solid black;
  border-radius: 5px;
  background-image: url('marble.jpg');
  padding: 10px 10px 10px 10px;
  
}
#interactive {
  position: relative;
  top:-5.3%;
  height:100%;
  width: 100%;
  text-align: center;
  font-family: 'dalek';
  background-color: white;
  border-radius: 5px;
  box-shadow: inset 1px 1px 1px black, inset -1px 1px 1px black, inset -1px -1px 1px black;
}
#account-balance {
  font-size: 35px;
  font-family: 'dalek';
  color: rgb(26, 25, 25);
  position: relative;
  top:3px;
  left:0.25%;
  width: 99.5%;
  background-color: white;


}
.balance-amount {
  font-family: 'dalek';
}

#history {
  position: relative;
  left:66.7%;
  top: -110%;
  height: 99.5%;
  width: 32.8%;
  font-size: 10px;
  background-color: white;
  border-radius: 5px;
  box-shadow: inset 1px 1px 1px black, inset -1px 1px 1px black, inset -1px -1px 1px black;
}
#history-title {
  font-family: 'dalek';
  position:relative;
  font-size: 20px;

}
#history-list {
  list-style-type: none;
  position: relative;
  left: -10%;
  text-align: center;
}
.history-item {
  position: relative;
  left:-15px;
  width: 120%;
  text-align: center;
}
#button-div {
  position: relative;
  top: 0;
}
.quick-btn {
  width:33.5%;
}
#withdrawBtn,
#depositBtn {
  width: 250px;
  height: 40px;
  border-radius: 10px;
  font-size: 20px;
  font-family: 'dalek';
  margin: 5px;
}
#history-button,
#end-session {
  height: 40px;
  width: 250px;
  border-radius: 10px;
  margin: 5px;
  font-size: 18px;
}
button:hover {
  background-color: black;
  color: white;
}

h2 {
  font-size: 35px;
  margin-bottom: -5%;
}
h3 {
  font-size: 25px;
}
#withdraw-div {
  position: relative;
  top:-20px;
  height: 200px;
}
#deposit-div {
  position: relative;
  top: 0px;
  height: 110px;
}
#other-withdraw {
  padding-top: 10px;
}
#withdraw,
#deposit {
  width: 60%;
  height: 30px;
  font-size: 20px;
}
#other-button,
#deposit-button {
  height: 35px;
  position: relative;
}
#deposit-button {
  top:-2px;
}
button {
  font-family: 'dalek';
}

#verification {
  position: relative;
  top: 25.5%;
  left: 15.2%;
  height: 405px;
  width: 670px;
  border: 2px solid black;
  border-radius: 5px;
  background-image: url('marble.jpg');
  padding: 10px 10px 10px 10px;
  font-family: 'dalek';
}

#inner-verification {
  background-color: white;
  position: relative;
  top:.5%;
  height: 100%;
  width: 100%;
  align-items: center;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  box-shadow: inset 1px 1px 1px black, inset -1px 1px 1px black, inset -1px -1px 1px black;
}

#verification-title,
#pin-display {
  position: absolute;
  top:0;
  text-align: center;
}
#pin-display {
  top: 15%;
}
#keypad {
  position: relative;
  top: 30px;
  background-image: url('marble.jpg');
}
.keypad-btn {
  width: 80px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.6);
  font-size: 20px;
  font-weight: bolder;
  margin:2px;
  box-shadow: 1px 1px 1px black,-1px -1px 1px black, 2px 2px 2px white, -2px -2px 2px white;
  text-shadow: 1px 1px black ;
  
}
#submit-btn {
  width: 250px;
  height: 40px;
  box-shadow: 1px 1px 1px black;
}
#atm-screen-div {
  width:1000px;
  height:700px;
}